exports.initMini = function () {
  var ua = navigator.userAgent;
  var isWx = /MicroMessenger/gi.test(ua);
  var isAlipay = /AlipayClient/gi.test(ua);
  var isSwan = /swan/gi.test(ua);

  if (isAlipay) {
    document.writeln('<script src="https://appx/web-view.min.js"' + '>' + '<' + '/' + 'script>');
  }

  if (isSwan) {
    document.writeln('<script src="https://b.bdstatic.com/searchbox/icms/searchbox/js/swan-2.0.6.js"' + '>' + '<' + '/' + 'script>');
  }

  if (isWx) {
    document.writeln('<script src="https://res.wx.qq.com/open/js/jweixin-1.3.2.js"' + '>' + '<' + '/' + 'script>');
  }
};

exports.getMiniProgramEnv = function () {
  return new Promise(function (resolve) {
    // 是否是京东客户端
    var isJD = navigator.userAgent.indexOf('jdapp') > -1;
    var isJDMP = isJD && navigator.userAgent.indexOf('jdmp') > -1;

    if (isJDMP) {
      resolve({
        isJD: isJD && isJDMP,
        isMiniProgram: isJD && isJDMP
      });
    } else if (window.my && window.my.getEnv) {
      window.my.getEnv(function (res) {
        resolve({
          isAlipay: res.miniprogram,
          isMiniProgram: res.miniprogram
        });
      });
    } else if (window.wx && window.wx.miniProgram) {
      window.wx.miniProgram.getEnv(function (res) {
        resolve({
          isWeapp: res.miniprogram,
          isMiniProgram: res.miniprogram
        });
      });
    } else if (window.swan && window.swan.webView.getEnv) {
      window.swan.webView.getEnv(function (res) {
        resolve({
          isSwan: res.smartprogram,
          isMiniProgram: res.smartprogram
        });
      });
    } else {
      resolve({
        isMiniProgram: false
      });
    }
  });
};